.dndflow {
    flex-direction: column;
    display: flex;
    flex-grow: 1;
    height: 100%;
  }


  .settings-menu {
    position: fixed;
    top: 10px;
    right: 50px;
    background: #2c2c2c;
    color: #fff;
    border: 1px solid #3a08ff;
    padding: 20px;
    border-radius: 8px;
    width: 300px;
    z-index: 10;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .settings-menu h3 {
    margin-bottom: 20px;
    font-size: 1cm;
  }
  
  .form-group {
    margin-bottom: 15px;
  }
  
  .form-group label {
    display: block;
    margin-bottom: 5px;
    font-weight: bold;
  }
  
  .form-control, .form-control-file, .form-control-range {
    width: 100%;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
  }
  
  .form-control:focus, .form-control-file:focus, .form-control-range:focus {
    border-color: #3a08ff;
    outline: none;
  }
  
  .select-control {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background-color: #444;
    color: #fff;
    padding: 10px;
    border-radius: 4px;
    border: 1px solid #ccc;
    font-size: 1em;
    cursor: pointer;
  }
  
  .select-control:hover, .select-control:focus {
    border-color: #3a08ff;
  }
  
  .select-control option {
    background-color: #2c2c2c;
    color: #fff;
  }
  
  .custom-file-upload {
    display: inline-block;
    padding: 8px 12px;
    cursor: pointer;
    background-color: #ff7308;
    color: #fff;
    border: none;
    border-radius: 4px;
    text-align: center;
    margin-top: 5px;
    font-size: 1em;
  }
  
  .custom-file-upload:hover {
    background-color: #f08400;
  }
  
  .file-name {
    display: inline-block;
    margin-left: 10px;
    font-size: 0.9em;
    color: #ccc;
  }
  
  .button-group {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
  }
  
  .btn {
    padding: 10px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 1em;
  }
  
  .btn-primary {
    background: #454546;
    color: #fff;
  }
  
  .btn-primary:hover {
    background: #39383e;
  }
  
  .btn-secondary {
    background: #6c757d;
    color: #fff;
  }
  
  .btn-secondary:hover {
    background: #5a6268;
  }
  
  



  .dndflow .react-flow__node {
    background-color: #f0f0f0;
    color: #3f51b5;
    font-size: 16px;
    padding: 12px 20px;
    border-radius: 20px;
    cursor: pointer;
    width:150px;
  }
  
  .dndflow .react-flow__node button {
    margin-left: 8px;
    background-color: #ff5e57;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 10px;
    padding: 0;
    height: 8px;
    width: 8px;
    display: flex;
    align-items: right;
    justify-content: center;
    line-height: 1;
  }
  
  .dndflow .react-flow__node button:hover {
    background-color: #e04848;
  }
  
  .dndflow .react-flow__node button:focus {
    outline: none;
  }
  
  .dndflow aside {
    border-right: 1px solid #eee;
    padding: 15px 10px;
    font-size: 12px;
    background: #fcfcfc;
  }
  
  .dndflow aside .description {
    margin-bottom: 10px;
  }
  
  .dndflow .dndnode {
    height: 20px;
    padding: 4px;
    border: 1px solid #1a192b;
    border-radius: 2px;
    margin-bottom: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: grab;
  }
  
  .dndflow .dndnode.input {
    border-color: #0041d0;
  }
  
  .dndflow .dndnode.output {
    border-color: #ff0072;
  }
  
  .dndflow .reactflow-wrapper {
    flex-grow: 1;
    height: 100%;
  }
  
  .dndflow .selectall {
    margin-top: 10px;
  }
  
  @media screen and (min-width: 768px) {
    .dndflow {
      flex-direction: row;
      flex-direction: row-reverse;
    }
  
    .dndflow aside {
      width: 15%;
      max-width: 250px;
    }
  }
  .react-flow__node-output, .react-flow__node-group {
    padding: 4px;
    border-radius: 3px;
    width: 100px;
    font-size: 10px;
    color: #222;
    text-align: center;
    border-width: 1px;
    border-style: solid;
    border-color: #1a192b;
    background-color: white;
}
