.settings-menu {
  padding: 20px;
  background-color: #f9f9f9;
  border: 1px solid #ddd;
  border-radius: 4px;
  max-width: 500px;
  max-height: 90vh; /* Limite la hauteur à 80% de la hauteur de la fenêtre */
  overflow-y: auto; /* Ajoute une barre de défilement verticale si le contenu dépasse la hauteur maximale */
  margin: auto;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.settings-menu h3 {
  margin-bottom: 20px;
}

.form-group {
  margin-bottom: 15px;
}

.form-group label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
}

.form-control,
.form-control-file,
.form-control-range {
  width: 100%;
  padding: 8px;
  margin-top: 5px;
}

.custom-file-upload {
  display: inline-block;
  padding: 6px 12px;
  cursor: pointer;
  background-color: #007bff;
  color: white;
  border-radius: 4px;
}

.file-name {
  margin-left: 10px;
  display: inline-block;
  vertical-align: middle;
}

.button-group {
  display: flex;
  justify-content: space-between;
}

.button-group .btn {
  padding: 10px 20px;
  cursor: pointer;
}

.btn-primary {
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
}

.btn-secondary {
  background-color: #6c757d;
  color: white;
  border: none;
  border-radius: 4px;
}

.tooltip {
  position: relative;
  display: inline-block;
  margin-left: 10px;
  cursor: pointer;
  color: #007bff;
}

.tooltip .tooltiptext {
  visibility: hidden;
  width: 220px;
  background-color: #555;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px;
  position: absolute;
  z-index: 1;
  bottom: 125%; /* Place le texte au-dessus de l'élément */
  left: 50%;
  margin-left: -110px;
  opacity: 0;
  transition: opacity 0.3s;
}

.tooltip:hover .tooltiptext {
  visibility: visible;
  opacity: 1;
}
