.sidebar {
  background-color: #ffffff;
  padding: 20px;
  border: 2px solid #3f51b5;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.dndnode {
  margin-bottom: 15px;
  padding: 10px;
  font-size: 18px;
  font-weight: bold;
  color: #333333;
  text-align: center;
  border: 2px solid #3f51b5;
  border-radius: 6px;
  background-color: #f0f0f0;
  cursor: pointer;
  transition: transform 0.3s ease, background-color 0.3s ease;
}

.dndnode:hover {
  background-color: #e0e0e0;
  transform: translateY(-3px);
}

.input {
  background-color: #e0f7fa;
}

.llms {
  background-color: #f0f0f0;
  color: #3f51b5;
  font-size: 16px;
  padding: 12px 20px;
  border-radius: 20px;
  cursor: pointer;
}

.output {
  background-color: #e8f5e9;
}

.toggleBtn {
  background-color: #3f51b5;
  color: #ffffff;
  border: none;
  padding: 12px 24px;
  font-size: 18px;
  font-weight: bold;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.toggleBtn:hover {
  background-color: #303f9f;
}
